.card{
    width: 250px;
    height: 380px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 30px;
    background-color: $backgroundCard;
    transition: transform 0.5 2s;
    &:hover{
        transform: scale(1.02);
    }

    &__link{
        width: 250px;
        height: 380px;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-content: center;
        align-items: center;
        gap: 10px;
        border-radius: 30px;
        font-family: $mainFont;
        font-size: 20px;
        color: white;
    }

    &__img{
        width: 230px;
        height: 250px;
        border-radius: 30px;
    }

    &__name{
        font-size: 25px;
        text-align: center;
        margin-top: 5px;
    }
    &__species{
        margin-bottom: 5px;
    }
}

@media screen and (min-width:768px){
    .card{
        width: 200px;
        height: 340px;

        &__link{
            width: 200px;
            height: 330px;
        }

        &__img{
            width: 170px;
            height: 250px;
            border-radius: 30px;
        }
    
        &__name{
            font-size: 25px;
            text-align: center;
            margin-top: 5px;
        }
        &__species{
            margin-bottom: 5px;
        }
    }
}

@media screen and (min-width:1080px){
    .card{
        width: 250px;
        height: 380px;
    
        &__link{
            width: 250px;
            height: 380px;
        }
    
        &__img{
            width: 230px;
            height: 250px;
        }
    }
}

//Class js status

.red{
    height: 34px;
    width: 80px;
    margin-left: 150px;
    font-size: 30px;
    font-weight: bold;
    text-transform: uppercase;
    color: $redAlive;
    text-align: center;
    border: 3px solid $redAlive;
    border-radius: 5px;
    transform: rotate(0.10turn) translateY(70%);
}

